// Colors
$primary-color: #9dc169;
$secondary-color: #a9dce2;

$background-color: #1a1a1a;
$background-color-secondary: #2c2c2c;
$text-color: #aaaaaa;
$text-color-dark: #6d6d6d;
$line-color: #aaaaaa;
$selection-text-color: #1d1d1d;
$selection-background-color: #c0c0c0;

// Size
$font-small: 12px;
$font-medium: 20px;
$font-large: 26px;
$font-xlarge: 30px;

// Universal pure classes
// Pure body, h1, p, a tags

body {
    color: $text-color;
    font-size: $font-medium;
    min-width: 320px;
    line-height: 1.5;
    // font-family: 'Source Code Pro', monospace;
    // font-family: 'Helvetica Neue', Helvetica, sans-serif;
    overflow-x: hidden;
    // overflow-y: hidden;
    min-height: 100%;
    margin: 0;
    visibility: visible;
    background-color: $background-color;
}

p, span, a, nav, h1, h2, h3, h4, h5, h6 {
    // font-family: 'Source Code Pro', monospace;
    // font-family: 'Helvetica Neue', Helvetica, sans-serif;
}

.text-dark {
    color: $text-color-dark;
}

hr {
    border: 0.5px solid $line-color;
}

ul {
    margin: 0;
    // padding: 0;
}

a:hover {
    background-color: $selection-background-color;
    color: $selection-text-color;
}

.hidden {
    display: none;
}

.flex {
    display: flex;
}

.pointer {
    cursor: pointer;
}

.home-banner {
    display: flex;
    flex-direction: row;
    margin: 50px 10vw;
    padding-bottom: 50px;
    max-width: 900px;

    &-text {
        margin-left: 20px;
        text-align: justify;
        text-justify: inter-word;
    }

    &-image {
        max-width: 320px;
        @media only screen and (max-width: 400px) {
            max-width: 100%;
        }
    }

    @media only screen and (max-width: 826px) {
        flex-direction: column-reverse;
        
        &-text {
            margin-bottom: 50px;
            margin-left: 0;
        }
    }
}

::selection {
    background: $selection-background-color;
}

// Custom Scrollbar
// width
::-webkit-scrollbar {
  width: 5px;
}
 
// Handle
::-webkit-scrollbar-thumb {
  background: $line-color; 
  border-radius: 10px;
}

// Handle on hover
::-webkit-scrollbar-thumb:hover {
  background: $secondary-color; 
}

.header {
    margin: 50px 10vw;
    max-width: 650px;
}

.title {
    font-size: $font-large;
}

.content {
    margin: 50px 10vw;
    padding-bottom: 50px;
    max-width: 650px;
    text-align: justify;
    text-justify: inter-word;
}

.list-single {
    margin: 15px 10px 10px 0px;
    list-style-type: none;
    
    @media only screen and (max-width: 650px) {
        display: flex;
        flex-direction: column;
        margin: 25px 10px 10px 0px;
    }
}

.tag-small {
    background-color: $background-color-secondary;
    padding: 2px 10px;
    margin: 5px;
    line-height: 1.75em;
    border-radius: 0px 100px 100px;
    max-width: fit-content;
    box-decoration-break: clone;

    // &:hover {
    //     background-color: $selection-background-color;
    // }
}

.post-tags {
    margin-top: 10px;
}

.back-to-top {
    position: fixed;
    bottom: 50px;
    right: 10vw;
    padding: 0 10px;
    font-size: 25px;
    background-color: $text-color;
    color: $background-color;
    display: none;
    &:hover {
        background-color: $background-color;        
        color: $text-color;
        cursor: pointer;
    }
}

code, pre {
    background-color: $background-color-secondary;
    border-radius: 2px;
}

th, td {
    border: solid $text-color 0.75px;
    padding: 5px 10px;
}

#link-list {
    display: flex;
    width: 100%;
    height: 100%;

    div {
        overflow: scroll;
        width: 100%;
        scroll-behavior: smooth;
    }

    .list {
        li {
            padding: 15px;
            border: solid #aaaaaa 1.5px;
            cursor: pointer;
            
            &:after {
                content: ">";
                text-align: right;
                float: right;
            }
            
            &:hover {
                border: solid white 1.5px;
                color: white;
            }
        }
    }
    
    .preview {
        li {
            padding: 15px;
            padding-bottom: 500px;
            border: dotted #aaaaaa 0.1px;
        }
    }
}

// navbar

nav {
    margin: 15px;
    border: 1.5px solid $line-color;
    border-radius: 5px;
    
    flex-direction: row;
    display: flex;

    overflow-x: scroll;
    
    a {
        padding: 7px 15px;
    }
}

// Code posts

.iframe-container {
    padding-bottom: 66.66%;
    padding-top: 25px;
    height: 0;

    margin-bottom: 50px;
    width: 1131px;
    height: 627px;
    
    // @media only screen and (max-width: 1200px) {
    //     width: 100%;
    // }

    iframe {
        width: 1131px;
        height: 627px;
        resize: both;
        overflow: auto;
        
        // @media only screen and (max-width: 1200px) {
        //     width: 100%;
        //     overflow: scroll;
        // }
    }
}
